export default {
	data() {
		return {
			name:'赵鸿飞',
			value:'',
			asd:'',
			shuju:false,
			input:'',//输入框内容
			code:'',
		}
	},
	onLoad: function (option) { //option为object类型，会序列化上个页面传递的参数
	  //       console.log(option.item); //打印出上个家伙传递的参数。
			// this.name = option.item
	    },
	methods: {
		onKeyInput:function(event){
			var that = this
			console.log(event.target.value)
			if (event.target.value == '') {
				
			} else {
				uni.request({
					url: this.$store.state.url + '/CRM/GetCustomerMini',
					method: 'GET',
					data: {
						'search': event.target.value
					},
					header: {
						'Token': this.$store.state.token
					},
					success: (res) => {
						that.asd = JSON.parse(res.data).Data.Dtos
						this.shuju = true
						console.log(that.asd)
					}
				});
			}
			
			
		},
		que(item){
			console.log(item.CusId)
			this.shuju = false
			this.input = item.FullName
			this.code = item.CusId
		},
		clicck:function(){//确认
			if(this.code != ''){
				this.$store.state.Customercode = this.code
				uni.navigateTo({
				        url: "./Spareparts/classification",
				    });
			}else{
				  uni.showToast({
				        title: "请输入客户",
				        icon: "none"
				    })
			}
		}
	}
}
